html {
    height: 100%;
    min-height: 100%;
}

body {
    padding: 0;
    margin: 0;
    background: #000 fixed url(../img/bg-wood.jpg) repeat top center;
    color: #fff;
    font: 16px/1.7 Arial, sans-serif;
}

.content {
    opacity: 0;
    transition: opacity 1s;
}

.loaded .content {
    opacity: 1;
}

h3, h4 {
    font-family: "American Captain", sans-serif;
    color: $color-text;
}

h3 {
    font-size: 40px;
}

h4 {
    font-size: 30px;
    margin-top: 25px;

    &:after {
        font-size: 16px;
    }
}

a {
    color: inherit;
    font-weight: bold;
    text-decoration: none;
}

.body {
    @include responsive(small) {
        border: 1px solid red;
    }
}

.logo {
    display: block;
    margin: 10px auto 30px;
    width: 100%;
    text-align: center;
    text-shadow: 0 5px 10px rgba(#000, 0.4);
    font-size: 7em;

    @include responsive(medium) {
        font-size: 12em;
    }
}

.flash {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: white;
    pointer-events: none;
}

.btn {
    display: block;
    border: 2px solid white;
    border-radius: 10px;
    padding: 15px 30px;
    margin: 10px auto 20px;
    width: 95%;
    max-width: 400px;
    background: rgba(#000, 0.2);
    font: 900 16px "Montserrat", sans-serif;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    color: white;

    small {
        display: block;
        font-weight: normal;
        font-size: 10px;
        text-transform: none;
    }
}

.btn-github i,
.btn-download i {
    display: block;
    margin-top: 5px;
    font-size: 2em;
}

.btn-github small,
.btn-download small {
    margin: 8px 0;
    opacity: 0.6;
}

.baseline {
    opacity: 0.9;
    font: 3.5em/1.1 "American Captain", sans-serif;
    margin-bottom: 30px ;
    text-shadow: 0 -3px 0 rgba(#000, 0.6);

    @include responsive(small) {
        & {
            font-size: 2em;
        }
    }
}

.tweet-it {
    width: 48px;
    height: 48px;
    border: 2px solid #fff;
    border-radius: 50%;
    display: block;
    padding: 10px;
    font-size: 24px !important;
    margin: 5px auto;
}

.donate-form {
    margin-bottom: 20px;

    input[type="text"] {
        text-align: right;
        border: 2px solid #000;
        border-radius: 4px;
        padding: 5px;
        text-transform: uppercase;
        color: #000;
        background: #fff;
    }

    input[type="submit"] {
        text-align: right;
        border: 2px solid #000;
        border-radius: 4px;
        padding: 5px;
        text-transform: uppercase;
        color: #000;
        background: none;
        cursor: pointer;
    }

    label {
        position:relative
    }

    label:after {
        content: '<>';
        font: 11px "Consolas", monospace;
        color: #666;
        transform: rotate(90deg);
        right: 10px;
        top: 2px;
        padding: 0 0 2px;
        border-bottom: 1px solid #ddd;
        position: absolute;
        pointer-events: none;
    }

    select {
        text-align: right;
        border: 2px solid #000;
        background: transparent;
        border-radius: 4px;
        padding: 5px;
        color: #000;
        padding-right: 25px;
        appearance: none;
    }

    @-moz-document url-prefix() {
        label:after {
            display: none;
        }

        select {
            padding: 5px;
        }
    }
}


