.demo-video-wrapper,
.demo-img-wrapper {
    position: relative;
    border: 10px solid white;
    box-shadow: 10px 10px 40px rgba(0, 0, 0, 0.5);
    background: #000;

    transform: rotate(2deg);
    transition: transform 6s cubic-bezier(0.075, 0.82, 0.165, 1);

    &:hover {
        transform: rotate(-1deg);
    }
}

    .demo-img-wrapper {
        margin: 20px auto 30px;
        max-width: 630px;
        width: 100%;
        border: 10px solid white;
    }

    .demo-video-wrapper {
        margin: -80px auto 30px;
        max-width: 280px;
        width: 100%;
        height: auto;
        border: 10px solid white;
        position: relative;

        .icon-play {
            position: absolute;
            top: 10px;
            left: 10px;
            font-size: 3em;
            text-shadow: 0 0 20px rgba(#000, 0.6);
            cursor: pointer;
        }
    }

.demo-img {
    display: block;
    width: 100%;
    height: auto;

    transition: 1s;
    filter: sepia(30%);

    &:hover {
        filter: sepia(0);
    }
}

.demo-video-click,
.demo-img-click {
    display: block;
    margin: -65px auto 0;
    position: relative;

    text-align: center;
    text-shadow: 0 10px 10px rgba(#000, 0.6);
    font-size: 8em;

    @include responsive(small) {
        font-size: 5em;
    }
}

.demo-video {
    width: 260px;
    height: 360px;
    vertical-align: top;
}

.face-img,
.face-video {
    position: absolute;
    border: 3px solid #fff;
    border-radius: 4px;
    box-shadow: 5px 5px 10px rgba(0,0,0,0.4);
}

.face-video {
    padding: 20px;
    transform: translate3d(-25%, -25%, 0);
}

.portraits {
    position: absolute;
    margin-top: -30px;
    width: 100%;
    left: 50%;
    text-align: center;
    z-index: 1;

    transform: translate(-50%, 0);
}
    .portrait {
        display: inline-block;
        width: 95px;
        height: 95px;
        margin: 0 2%;
        border-radius: 100px;
        border: 3px solid white;
        box-shadow: 10px 10px 40px rgba(0, 0, 0, 0.5);
        cursor: pointer;
        filter: sepia(40%);
    }

.overlay {
    display: none;
}

.mobile {
    .demo-video-wrapper {
        margin-top: 0;
    }

    .hide-on-mobile {
        width: 100%;
        position: relative;

        .overlay {
            display: block;
            position: absolute;
            background-color: rgba(#333, 0.8);
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;

            span {
                color: #fff;
                font-size: 1em;
                font-weight: bold;
                text-align: center;
                position: absolute;
                top: 50%;
                left: 50%;
                width: 90%;
                transform: translateX(-50%);
            }
        }
    }
}