// ---------------------------------------------------
// #Variables
// --------------------------------------------------

$grid-cols:         12   !default;
$grid-gutters:      20px !default;

$screen-medium:    768px !default;
$screen-large:     992px !default;

$container-medium: 750px !default;
$container-large:  970px !default;

// ---------------------------------------------------
// #Mixins & Misc
// ---------------------------------------------------

@mixin box-sizing() {
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
      -ms-box-sizing: border-box;
       -o-box-sizing: border-box;
          box-sizing: border-box;
}

@mixin centered() {
    margin-left: auto; 
    margin-right: auto; 
    float: none; 
}

@mixin responsive($type) {
    @if ($type == small) {
        @media (max-width: $screen-medium) {
            @content;
        }
    } @else if ($type == medium) {
        @media (min-width: $screen-medium) {
            @content;
        } 
    } @else if ($type == large) {
        @media (min-width: $screen-large) {
            @content;
        }
    } @else {
        @warn "Responsive mixim only accepts small, medium or large parameters";
    }
}

