// ---------------------------------------------------
// #Classes
// ---------------------------------------------------

.container {
    margin: 0 auto;
}

.row {
    @include box-sizing();

    display: block;
    margin: 0 ($grid-gutters * -0.5) $grid-gutters;

    &:before,
    &:after {
         content: " ";
         display: block;
         clear: both;
         visibility: hidden;
         line-height: 0;
         height: 0;
    }
}

%all-col {
    @include box-sizing();

    position: relative;
    min-height: 1px;
    padding: 0 $grid-gutters * 0.5;
}

@for $i from 1 through $grid-cols {
    .small-#{$i},
    .medium-#{$i},
    .large-#{$i} {
        @extend %all-col;
    }
}

// ---------------------------------------------------
// #Small
// ---------------------------------------------------

%small-col {
    float: left;
}

@for $i from 1 through $grid-cols {
    .small-#{$i} {
        @extend %small-col;

        width: $i / $grid-cols * 100%;
    }
}

.small-hidden,
.medium-visible,
.large-visible {
    display: none;
}

.small-visible,
.medium-hidden,
.large-hidden { 
    display: block;
}

.small-centered { 
    @include centered();
}

// Small only

@media (max-width: $screen-medium) {
    .small-only-centered { 
        @include centered();
    }
}

// ---------------------------------------------------
// #Medium
// ---------------------------------------------------

@media (min-width: $screen-medium) {
    .container {
        width: $container-medium;
    }

    %medium-col {
        float: left;
    }

    @for $i from 1 through $grid-cols {
        .medium-#{$i} {
            @extend %medium-col;

            width: $i / $grid-cols * 100%;
        }
    }

    .small-hidden,
    .medium-visible,
    .large-hidden { 
        display: block; 
    }

    .small-visible,
    .medium-hidden,
    .large-visible { 
        display: none;
    }

    .medium-centered { 
        @include centered();
    }

    .medium-uncentered {
        float: left; 
    }
}

// Medium only

@media (min-width: $screen-medium) and (max-width: $screen-large) {
    .medium-only-centered { 
        @include centered();
    }
}

// ---------------------------------------------------
// #Large
// ---------------------------------------------------

@media (min-width: $screen-large) {
    .container {
        width: $container-large;
    }

    %large-col {
        float: left;
    }

    @for $i from 1 through $grid-cols {
        .large-#{$i} {
            @extend %large-col;

            width: $i / $grid-cols * 100%;
        }
    }

    .small-hidden,
    .medium-hidden,
    .large-visible { 
        display: block; 
    }

    .small-visible,
    .medium-visible,
    .large-hidden { 
        display: none;
    }

    .large-centered,
    .large-only-centered { 
        @include centered();
    }

    .large-uncentered {
        float: left; 
    }
}
