.container {
    padding: 40px 20px;

    @include responsive(small) {
        padding: 30px 10px;
    }
}

.separator {
    .container {
        padding: 20px 20px !important;
    }

    .icon-logo {
        text-shadow: 0 5px 20px rgba(#000, 0.2);
        padding: 0;
        margin: 0;
        font-size: 8em;
        color: $color-text;

        @include responsive(small) {
            font-size: 6em;
        }
    }
}

.splash {
    color: #fff;
    box-shadow: 0 -10px 50px rgba(0,0,0,0.5) inset;
    position: relative;
}

.block {
    background: #eee url(../img/bg-paper.jpg) repeat top center;
    text-shadow: 0 2px 0 white;
    text-align: center;
    color: #000;

    h3, h4 {
        font-family: "American Captain", sans-serif;
        color: $color-text;

        &:after {
            margin-top: -10px;
            display: block;
            text-align: center;
            font-size: 20px;
            content: "★";
        }
    }

    p {
        padding-bottom: 10px;
    }
}

.block-alt {
    position: relative;
    overflow: hidden;
    box-shadow: 0 0px 50px rgba(0,0,0,0.5) inset;
    text-shadow: 1px -2px 1px rgba(0,0,0,0.5);
    text-align: center;

    .icon-background.icon-github {
        font-size: 1000px;
        position: absolute;
        top: -255px;
        left: 50%;
        opacity: 0.2;
        color: #000;

        transform: translateX(-43%);
    }

    .container {
        padding: 100px 10%;
    }
}

.footer {
    text-align: center;
    background: #000;
    color: #fff;

    .icon-logo {
        margin: 20px;
        color: #fff;
        display: block;
        font-size: 6em;

        @include responsive(small) {
            font-size: 5em;
        }
    }
}

.footer-copyright {
    color: #999;
    font-size: 10px;
}