.sponsors {
   transition-delay: 3s;
}

#adblocked,
#carbonads {
    display: block;
    width: 190px;
    margin: 40px auto 0;
    min-height: 160px;

    @include responsive(large) {
        position: absolute;
        margin: 0;

        bottom: 20px;
        right: 20px;
        width: 130px;
        height: auto;
    }
}
    .carbon-img {
        display: block;
        margin: 5px auto;
        text-align: center;
    }

    .carbon-img img {
        width: 130px;
        height: auto;
        box-shadow: 0 0 20px #000;
        border: 1px solid #fff;
        background: #eee;
    }

    .carbon-text,
    .carbon-poweredby {
        font-weight: normal;
        text-decoration: none;
        color: #eee;
        font-size: 11px;
        display: block;
        text-align: center;

        b {
            color: #fff;
            font-weight: bold;
        }
    }

    .carbon-poweredby {
        font-size: 9px;
        display: block;
        opacity: 0.8;
    }
